@include tablet {
    .contenedor-anuncios {
        @include grid(2, 2rem); 
    }
}

@include desktop {
    .contenedor-anuncios {
        @include grid(3, 2rem);
    }
}

.anuncio {
    background-color: lighten($gris, 5);
    border: 1px solid lighten($grisOscuro, 10);
    margin-bottom: 2rem;
    .contenido-anuncio {
        padding: 2rem;

    }

    h3, p {
        margin: 0;
    }
    
}

.precio {
    color: $verde;
    font-weight: $bold;
    font-size: 3rem;
}

.iconos-caracteristicas {
    display: flex;
    list-style: none;
    padding: 0;
    max-width: 40rem;

    li {
        flex: 1;
        display: flex;

        img {
            flex: 0 0 5rem;
            margin-right: 1rem;
        }
        p {
            font-weight: $black;
        }
    }
}

.comentario {
    background-color: $morado;
    padding: 3rem;
    border-radius: 2rem;
    margin-bottom: 3rem;

    p {
        color: $blanco;
    }
    .iluminada {
        color: $amarillo;
    }
}

#map {
    display: flex;
    justify-content: center;
    width: 100%; 
    height: 400px;
}

